<template>
  <view class="pay-container">
    <view class="pay-card">
      <view class="card-header">
        <text class="title">支付测试</text>
      </view>
      
      <view class="order-info">
        <view class="info-item">
          <text class="label">商品名称</text>
          <text class="value">测试商品</text>
        </view>
        <view class="info-item">
          <text class="label">支付金额</text>
          <text class="value price">¥ 0.01</text>
        </view>
        <view class="info-item">
          <text class="label">订单编号</text>
          <text class="value">{{orderId}}</text>
        </view>
      </view>
      
      <view class="pay-method">
        <view class="method-item active">
          <view class="method-icon">
            <uni-icons type="weixin" size="24" color="#07C160"></uni-icons>
          </view>
          <view class="method-info">
            <text class="method-name">微信支付</text>
          </view>
          <view class="check-icon">
            <uni-icons type="checkmarkempty" size="18" color="#FF6B01"></uni-icons>
          </view>
        </view>
      </view>
      
      <button class="pay-btn" @click="handlePay" :loading="loading">
        立即支付
      </button>
      
      <!-- 支付状态提示 -->
      <view class="pay-status" v-if="payStatus">
        <uni-icons :type="payStatus === 'success' ? 'checkbox-filled' : 'closeempty'" 
                  :color="payStatus === 'success' ? '#52C41A' : '#F56C6C'"
                  size="64"></uni-icons>
        <text class="status-text" :class="payStatus">
          {{payStatus === 'success' ? '支付成功' : '支付失败'}}
        </text>
        <text class="status-desc" v-if="payMessage">{{payMessage}}</text>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      orderId: '', // 订单ID
      loading: false, // 支付按钮加载状态
      payStatus: '', // 支付状态: 'success' 或 'fail'
      payMessage: '', // 支付结果消息
    }
  },
  
  onLoad() {
    // 生成随机订单号
    this.orderId = 'TEST' + new Date().getTime() + Math.floor(Math.random() * 1000);
  },
  
  methods: {
    // 处理支付
    async handlePay() {
      this.loading = true;
      this.payStatus = '';
      this.payMessage = '';
      
      try {
        // 获取用户openId，这里假设已存储在storage中
        const openId = uni.getStorageSync('openId');
        if (!openId) {
          uni.showToast({
            title: '请先登录',
            icon: 'none'
          });
          this.loading = false;
          return;
        }
        
        // 调用创建订单接口
        const res = await this.createPayOrder(openId);
        
        if (res.code === 200 && res.data) {
          // 调起微信支付
          this.callWxPay(res.data);
        } else {
          this.showPayResult(false, res.message || '创建支付订单失败');
        }
      } catch (error) {
        console.error('支付请求异常', error);
        this.showPayResult(false, '支付异常：' + (error.message || '未知错误'));
      } finally {
        this.loading = false;
      }
    },
    
    // 创建支付订单
    createPayOrder(openId) {
      return new Promise((resolve, reject) => {
        uni.request({
          url: uni.getStorageSync('baseUrl') + '/WxPay/createOrder',
          method: 'POST',
          header: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Authorization': uni.getStorageSync('token')
          },
          data: {
            orderId: this.orderId,
            openId: openId,
            amount: 0.01, // 支付金额固定0.01元
            description: '测试商品'
          },
          success: (res) => {
            console.log('支付订单创建结果:', res);
            resolve(res.data);
          },
          fail: (err) => {
            console.error('支付订单创建失败:', err);
            reject(err);
          }
        });
      });
    },
    
    // 调起微信支付
    callWxPay(payParams) {
      uni.requestPayment({
        ...payParams,
        success: (res) => {
          console.log('支付成功', res);
          this.showPayResult(true, '支付成功');
          // 支付成功后查询订单状态
          this.queryOrderStatus();
        },
        fail: (err) => {
          console.log('支付失败', err);
          if (err.errMsg === 'requestPayment:fail cancel') {
            this.showPayResult(false, '您已取消支付');
          } else {
            this.showPayResult(false, '支付失败：' + err.errMsg);
          }
        }
      });
    },
    
    // 查询订单状态
    queryOrderStatus() {
      uni.request({
        url: uni.getStorageSync('baseUrl') + `/WxPay/queryOrder`,
        method: 'GET',
        header: {
          'Authorization': uni.getStorageSync('token')
        },
        data: {
          orderId: this.orderId
        },
        success: (res) => {
          console.log('订单状态查询结果:', res.data);
          if (res.data.code === 200 && res.data.data) {
            const state = res.data.data.trade_state;
            if (state === 'SUCCESS') {
              this.showPayResult(true, '支付成功');
            } else if (state === 'NOTPAY' || state === 'USERPAYING') {
              this.showPayResult(false, '支付处理中，请稍候');
            } else {
              this.showPayResult(false, res.data.data.trade_state_desc || '支付异常');
            }
          }
        },
        fail: (err) => {
          console.error('查询订单状态失败', err);
        }
      });
    },
    
    // 显示支付结果
    showPayResult(success, message) {
      this.payStatus = success ? 'success' : 'fail';
      this.payMessage = message;
      
      // 显示提示
      uni.showToast({
        title: message,
        icon: success ? 'success' : 'none',
        duration: 2000
      });
      
      // 如果支付成功，3秒后返回或跳转
      if (success) {
        setTimeout(() => {
          // 根据实际需求决定是返回上一页还是跳转到订单页
          // uni.navigateBack();
          // 或者跳转到订单页
          // uni.redirectTo({
          //   url: '/pages/order/detail?id=' + this.orderId
          // });
        }, 3000);
      }
    }
  }
}
</script>

<style lang="scss">
.pay-container {
  padding: 30rpx;
  min-height: 100vh;
  background: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pay-card {
  width: 100%;
  background: #fff;
  border-radius: 16rpx;
  padding: 40rpx;
  box-shadow: 0 4rpx 16rpx rgba(0,0,0,0.05);
  margin-top: 40rpx;
  
  .card-header {
    text-align: center;
    margin-bottom: 40rpx;
    padding-bottom: 30rpx;
    border-bottom: 1px solid #f0f0f0;
    
    .title {
      font-size: 36rpx;
      font-weight: bold;
      color: #333;
    }
  }
  
  .order-info {
    margin-bottom: 40rpx;
    
    .info-item {
      display: flex;
      justify-content: space-between;
      margin-bottom: 20rpx;
      
      .label {
        color: #666;
        font-size: 28rpx;
      }
      
      .value {
        color: #333;
        font-size: 28rpx;
        
        &.price {
          color: #FF6B01;
          font-size: 32rpx;
          font-weight: bold;
        }
      }
    }
  }
  
  .pay-method {
    background: #f9f9f9;
    border-radius: 12rpx;
    padding: 20rpx;
    margin-bottom: 60rpx;
    
    .method-item {
      display: flex;
      align-items: center;
      padding: 20rpx;
      
      &.active {
        background: rgba(255, 107, 1, 0.05);
        border-radius: 8rpx;
      }
      
      .method-icon {
        margin-right: 20rpx;
      }
      
      .method-info {
        flex: 1;
        
        .method-name {
          font-size: 28rpx;
          color: #333;
        }
      }
    }
  }
  
  .pay-btn {
    width: 100%;
    height: 88rpx;
    line-height: 88rpx;
    background: linear-gradient(90deg, #FF6B01, #FF8F1F);
    border-radius: 44rpx;
    color: #fff;
    font-size: 32rpx;
    font-weight: 500;
    margin-top: 30rpx;
    box-shadow: 0 8rpx 16rpx rgba(255, 107, 1, 0.2);
    border: none;
    
    &::after {
      border: none;
    }
    
    &[loading] {
      background: #ccc;
      box-shadow: none;
    }
  }
  
  .pay-status {
    margin-top: 60rpx;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .status-text {
      margin-top: 20rpx;
      font-size: 32rpx;
      font-weight: bold;
      
      &.success {
        color: #52C41A;
      }
      
      &.fail {
        color: #F56C6C;
      }
    }
    
    .status-desc {
      margin-top: 10rpx;
      font-size: 26rpx;
      color: #999;
      text-align: center;
    }
  }
}
</style> 